.app {
  background-color: #0e0c11;
  font-family: "Roboto";
  color: white;
  min-width: 100%;
  overflow: hidden;
}

.header {
  background-color: #24202B;
  min-height: 3em;
  display: flex;
  align-content: space-between;
  font-size: calc(3px + 2vmin); 
  color: white;
  flex-direction: row;
  align-items: center;
  min-width: 100%;
}

.header a{
  margin-right: 5vh;
  margin-left: 5vh;
  display: inline-block;
  text-decoration: none;
  color: white;
}

.header a:visited{
  color: white;
  text-decoration: none;
}

.header a:hover{
  color: #CE2D4F;
  text-decoration: none;
}

.header-left{
  margin-left: 5vh;
  margin-right: auto;
  display: inline-block;
}

.header-right{
  margin-right: 10vh;
}

.button-image{
  background: none;
  margin: 0;
  border-radius: 0;
  box-shadow: none;
  width: auto;
  height: auto;
}

.language-image{
  height: 20px;
}

h1 {
  font-family: "Courier New";
  font-size: 50px;
  margin-top: 8vh;
  margin-left: 10vh;
  margin-bottom: 0vh;
}

.main{
  margin-top: 10vh;
}

.main-image{
  height: 500px;
  margin-left: 2vh;
}

.main-image-pt{
  height: 500px;
  margin-left: 2vh;
  margin-top: -6vh
}

.row-basic{
  display: flex;
  align-items: flex-end;
  margin-bottom: 5vh;
}

.row-basic h1 {
  margin-top: 0vh;
}

article {
  margin-left: 10vh;
  margin-right: 10vh;
  font-size: 30px;
  text-align: justify;
  text-justify: inter-word;
}

.portfolio-call {
  margin-top: 2vh;
}

button {
  background-color: #CE2D4F;
  color: #ffffff;
  border-radius: 20px;
  width: 30vh;
  height: 6.5vh;
  font-size: 20px;
  text-align: center;
  margin-left: 10vh;
  margin-top: 4vh;
  border: none;
  box-shadow: 2px 2px 5px #24202B;
  cursor: pointer;
}

button a {
  color: white;
  text-decoration: none;
}

.h1-title{
  margin-top: 20vh;
}

.blink-image {
  height: 50px;
  margin-bottom: 1.5vh;
  margin-left: -0.5vh;
  -moz-animation: blink normal 1.8s steps(2) infinite; /* Firefox */
  -webkit-animation: blink normal 1.8s steps(2) infinite; /* Webkit */
  -ms-animation: blink normal 1.8s steps(2) infinite; /* IE */
  animation: blink normal 1.8s steps(2) infinite; /* Opera and prob css3 final iteration */
}

#about article{
  margin-right: 10vh;
}

.about-container{
  display: flex;
  background-color: #24202B;
  height: 30vh;
  margin-left: 10vh;
  margin-right: 10vh;
  margin-top: 5vh;
  margin-bottom: 5vh;
  border-radius: 20px;
  align-items: center;
  padding-left: 20vh;
  color: white;
}

.about-container-right{
  display: flex;
  background-color: #24202B;
  height: 30vh;
  margin-left: 10vh;
  margin-right: 10vh;
  margin-top: 5vh;
  border-radius: 20px;
  align-items: center;
  justify-content: flex-end;
  padding-right: 20vh;
  color: white;
}

.about-image{
  height: 100px;
}

.about-article{
  margin-left: 0;
  margin-right: 0;
  line-height: 1.5;
}

.about-article-right{
  margin-right: 0;
  margin-left: 0;
  line-height: 1.5;
  text-align: right;
}

.vertical-line {
  border-left: 3px solid rgb(70, 70, 70);
  height: 20vh;
  margin-right: 5vh;
  margin-left: 5vh;
}

.vertical-line-right{
  border-left: 3px solid rgb(70, 70, 70);
  height: 20vh;
  margin-right: 5vh;
  margin-left: -5vh;
}

.contact-container{
  display: flex;
  background-color: #24202B;
  height: 30vh;
  margin-left: 10vh;
  margin-right: 10vh;
  margin-top: 5vh;
  margin-bottom: 10vh;
  border-radius: 20px;
  align-items: center;
  justify-content: center;
  color: white;
}

h3 {
  margin-bottom: 0vh;
}

.contact-item{
  margin-left: 10vh;
  margin-right: 10vh;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.contact-item a{
  text-decoration: none;
  color: white;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.contact-image{
  height: 50px;
}

footer{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.bird-image{
  height: 70px;
  margin-bottom: 5vh;
}

footer p {
  font-size: 15px;
}

footer a {
  color: white;
  margin-left: 1vh;
}

/* - - - - - - - - - - - - - - - - PORTFOLIO CSS - - - - - - - - - - - - - - - - */

.arrow-right {
  width: 0; 
  height: 0; 
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-left: 10px solid #21897E;
  margin-right: 2vh;
}

.arrow-down {
  width: 0; 
  height: 0; 
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid #21897E;
  margin-right: 2vh;
}

.row-arrow{
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: 10vh;
  margin-top: 5vh;
}

.row-arrow h2 {
  font-size: 30px;
}

/* - - - - - - - - - - - - - - - - GALLERY CSS - - - - - - - - - - - - - - - - */
.gallery{
  margin-left: 5vh;
  margin-right: 5vh;
}

.thumbnail {
  margin-left: 2vh;
  margin-right: 2vh;
}

.ReactCollapse--collapse {
  transition: height 500ms;
}

.click-image {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #24202B;
  backdrop-filter: blur(5px);
}

.expand-image {
  -moz-transform: scale(2);
  -ms-transform: scale(2);
  -o-transform: scale(2);
  -webkit-transform: scale(2);
  transform: scale(2);
}

.lightbox{
  --yarl__color_backdrop: rgba(14, 12, 17, 1);
  --yarl__color_button: rgba(255, 255, 255, 1);
  --yarl__button_background_color: rgba(33, 137, 126, 1);
  --yarl__button_margin: 1.5vh;
  --yarl__button_padding: 0;
  --yarl__slide_title_color: rgba(255, 255, 255, 1);
  --yarl__slide_description_color: rgba(255, 255, 255, 1);
  --yarl__icon_size: 25px;
  --yarl__toolbar_padding: 0;
  --yarl__navigation_prev_padding: 0;
}

.photo-album{
  padding-left: 5vh;
  padding-right: 5vh;
  padding-bottom: 10vh;
}

.horizontal-line{
  width: 95%;
  border-bottom: 3px solid #21897E;
  margin-right: 2vh;
}

.row-year{
  display: flex;
  flex-direction: row;
  align-items: center;
}

.row-year h2{
  color: #21897E;
  font-weight: bolder;
}

.bird-image-portfolio{
  height: 70px;
  margin-bottom: 50vh;
  margin-top: 20vh;
}

/* - - - - - - - - - - - - - - - - BLINK ANIMATION - - - - - - - - - - - - - - - - */

@-moz-keyframes blink {
  0% {
    opacity: 0;
  }
} 

@-webkit-keyframes blink {
  0% {
    opacity: 0;
  }
}
/* IE */
@-ms-keyframes blink {
  0% {
    opacity: 0;
  }
} 
/* Opera and prob css3 final iteration */
@keyframes blink {
  0% {
    opacity: 0;
  }
} 

/* - - - - - - - - - - - - - - - - BURGER MENU CSS - - - - - - - - - - - - - - - - */
/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 36px;
  top: 36px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #373a47;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #CE2D4F;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #CE2D4F;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%;
}

/* General sidebar styles */
.bm-menu {
  background: #24202B;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: white;
  padding: 0.8em;
}

/* Individual item */
.bm-item {
  display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

/* - - - - - - - - - - - - - - - - MOBILE - - - - - - - - - - - - - - - - */
@media screen and (max-width: 1000px){
  .header{
    background-color: #24202B;
    min-height: 5em;
    display: flex;
    align-content: space-between;
    color: white;
    flex-direction: row;
    align-items: center;
    min-width: 100%;
  }

  .main{
    margin-top: 5vh;
    margin-left: -15vw;
    margin-right: 0;
  }

  h1 {
    font-family: "Courier New";
    font-size: 9vw;
  }

  article {
    font-size: 6vw;
    text-align: justify;
    text-justify: inter-word;
  }
  
  .main-image{
    display: none;
  }
  
  .main-image-pt{
    display: none;
  }

  .main-image-mobile{
    height: 40vh;
    margin-left: 5vw;
    align-self: center;
  }

}

@media screen and (max-width: 500px){
  .blink-image {
    height: 6vw;
    margin-bottom: 1vh;
    margin-left: -10vw;
    -moz-animation: blink normal 1.8s steps(2) infinite; /* Firefox */
    -webkit-animation: blink normal 1.8s steps(2) infinite; /* Webkit */
    -ms-animation: blink normal 1.8s steps(2) infinite; /* IE */
    animation: blink normal 1.8s steps(2) infinite; /* Opera and prob css3 final iteration */
  }
}

